import {getChartData} from '../../apis';

let recordTimer = 0;

export default {

  mounted() {

  },

  data(){
    return {
      recording: false,
      pending: false
    }
  },

  computed: {
    primaryTip() {
      if (this.pending && !this.recording) {
        return '正在识别并查询...';
      } else if (this.recording) {
        return '正在接收语音...';
      } else {
        return '点击说出指令';
      }
    },

    secondaryTip() {
      if (this.pending && !this.recording) {
        return '';
      } else if (this.recording) {
        return '点击停止说话';
      } else {
        return '通过语音查询数据报告';
      }
    }
  },

  methods: {
    // 录音开始/结束
    handleToggleRecord() {
      if (this.recording) {
        this.stopRecord();
      } else {
        // 调用微信录音接口
        wx.startRecord({
          cancel: () => {
            this.$vux.toast.show({
              type: 'warn',
              text: '未授权录音'
            });
          }
        });

        this.recording = true;
        // 10秒后自动结束录音
        recordTimer = setTimeout(() => {
          this.stopRecord();
        }, 10000);
      }
    },

    // 录音结束
    stopRecord(){
      // 取消定时操作
      clearTimeout(recordTimer);
      // 调用微信sdk结束录音
      wx.stopRecord({
        success: res => {
          const localId = res.localId;
          wx.translateVoice({
            localId,
            isShowProgressTips: 0,
            complete: res => {
              if (res.hasOwnProperty('translateResult')) {
                console.log('识别结果：' + res.translateResult);
                this.$vux.toast.show({
                  type: 'success',
                  text: '识别结果：' + res.translateResult
                });
                getChartData({
                  lang_texts: [res.translateResult]
                });
              } else {
                this.$vux.toast.show({
                  type: 'warn',
                  text: '无法识别'
                });
              }
            }
          })
        }
      });

      this.recording = false;
    }
  }
}



